import React from 'react';
import { WalletType, WalletConnectModalProps } from '../types';

const WalletSelectionModal: React.FC<WalletConnectModalProps> = ({
  isOpen = false,
  onWalletSelect,
  onClose,
}) => {
  if (!isOpen) return null;

  const wallets: WalletType[] = ['MetaMask', 'Phantom', 'Rabby'];

  const handleWalletClick = (wallet: WalletType) => {
    if (onWalletSelect) {
      onWalletSelect(wallet);
    }
  };

  const getWalletIcon = (wallet: WalletType): string => {
    switch (wallet) {
      case 'MetaMask':
        return 'ü¶ä';
      case 'Phantom':
        return 'üëª';
      case 'Rabby':
        return 'üê∞';
      default:
        return 'üíº';
    }
  };

  return (
    <div
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0, 0, 0, 0.5)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        zIndex: 1000,
      }}
      onClick={onClose}
    >
      <div
        style={{
          backgroundColor: '#fff',
          borderRadius: '16px',
          padding: '32px',
          maxWidth: '400px',
          width: '90%',
          boxShadow: '0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)',
        }}
        onClick={(e) => e.stopPropagation()}
      >
        <div
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginBottom: '24px',
          }}
        >
          <h2
            style={{
              margin: 0,
              fontSize: '24px',
              fontWeight: '700',
              color: '#1F2937',
            }}
          >
            Select Wallet
          </h2>
          <button
            onClick={onClose}
            style={{
              background: 'none',
              border: 'none',
              fontSize: '24px',
              cursor: 'pointer',
              color: '#6B7280',
              padding: '0',
              width: '32px',
              height: '32px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              borderRadius: '4px',
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor = '#F3F4F6';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor = 'transparent';
            }}
          >
            √ó
          </button>
        </div>

        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            gap: '12px',
          }}
        >
          {wallets.map((wallet) => (
            <button
              key={wallet}
              onClick={() => handleWalletClick(wallet)}
              style={{
                display: 'flex',
                alignItems: 'center',
                gap: '16px',
                padding: '16px',
                border: '2px solid #E5E7EB',
                borderRadius: '12px',
                backgroundColor: '#fff',
                cursor: 'pointer',
                transition: 'all 0.2s ease',
                textAlign: 'left',
                width: '100%',
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.borderColor = '#4F46E5';
                e.currentTarget.style.backgroundColor = '#EEF2FF';
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.borderColor = '#E5E7EB';
                e.currentTarget.style.backgroundColor = '#fff';
              }}
            >
              <span style={{ fontSize: '32px' }}>{getWalletIcon(wallet)}</span>
              <span
                style={{
                  fontSize: '18px',
                  fontWeight: '600',
                  color: '#1F2937',
                }}
              >
                {wallet}
              </span>
            </button>
          ))}
        </div>
      </div>
    </div>
  );
};

export default WalletSelectionModal;

