import React from 'react';
import { CustomWalletModalProps } from '../types';

const CustomWalletModal: React.FC<CustomWalletModalProps> = ({
  wallet,
  isOpen = false,
  onClose,
}) => {
  if (!isOpen) return null;

  const getWalletInfo = () => {
    switch (wallet) {
      case 'MetaMask':
        return {
          icon: 'ü¶ä',
          name: 'MetaMask',
          description: 'Connect to your MetaMask wallet',
          color: '#F6851B',
        };
      case 'Phantom':
        return {
          icon: 'üëª',
          name: 'Phantom',
          description: 'Connect to your Phantom wallet',
          color: '#AB9FF2',
        };
      case 'Rabby':
        return {
          icon: 'üê∞',
          name: 'Rabby',
          description: 'Connect to your Rabby wallet',
          color: '#FF6B6B',
        };
      default:
        return {
          icon: 'üíº',
          name: wallet,
          description: 'Connect to your wallet',
          color: '#4F46E5',
        };
    }
  };

  const walletInfo = getWalletInfo();

  return (
    <div
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0, 0, 0, 0.5)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        zIndex: 1001,
      }}
      onClick={onClose}
    >
      <div
        style={{
          backgroundColor: '#fff',
          borderRadius: '16px',
          padding: '40px',
          maxWidth: '450px',
          width: '90%',
          boxShadow: '0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)',
          textAlign: 'center',
        }}
        onClick={(e) => e.stopPropagation()}
      >
        <div
          style={{
            fontSize: '64px',
            marginBottom: '16px',
          }}
        >
          {walletInfo.icon}
        </div>

        <h2
          style={{
            margin: '0 0 8px 0',
            fontSize: '28px',
            fontWeight: '700',
            color: '#1F2937',
          }}
        >
          {walletInfo.name}
        </h2>

        <p
          style={{
            margin: '0 0 32px 0',
            fontSize: '16px',
            color: '#6B7280',
          }}
        >
          {walletInfo.description}
        </p>

        <div
          style={{
            padding: '24px',
            backgroundColor: '#F9FAFB',
            borderRadius: '12px',
            marginBottom: '24px',
          }}
        >
          <p
            style={{
              margin: 0,
              fontSize: '14px',
              color: '#4B5563',
              lineHeight: '1.6',
            }}
          >
            This is a customized modal for {walletInfo.name}. In a real implementation, this would
            handle the wallet connection logic.
          </p>
        </div>

        <div
          style={{
            display: 'flex',
            gap: '12px',
            justifyContent: 'center',
          }}
        >
          <button
            onClick={onClose}
            style={{
              padding: '12px 24px',
              fontSize: '16px',
              fontWeight: '600',
              color: '#6B7280',
              backgroundColor: '#F3F4F6',
              border: 'none',
              borderRadius: '8px',
              cursor: 'pointer',
              transition: 'all 0.2s ease',
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor = '#E5E7EB';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor = '#F3F4F6';
            }}
          >
            Cancel
          </button>
          <button
            onClick={onClose}
            style={{
              padding: '12px 24px',
              fontSize: '16px',
              fontWeight: '600',
              color: '#fff',
              backgroundColor: walletInfo.color,
              border: 'none',
              borderRadius: '8px',
              cursor: 'pointer',
              transition: 'all 0.2s ease',
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.opacity = '0.9';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.opacity = '1';
            }}
          >
            Connect {walletInfo.name}
          </button>
        </div>
      </div>
    </div>
  );
};

export default CustomWalletModal;

